import React, { Component } from 'react';
import "../Styles/RatingButton.css"

class RatingButton extends Component {
    render() {
        const style = this.props.type === "gold" ? "btn-warning btn-gold" : "btn-light";
        const disabled = this.props.disabled === "true" ? "btn-dis" : "";
        return (
            <span className="pr-1">
                <button type="button" className={"btn " + style + " btn-sm " + disabled} aria-label="Left Align">
                    <i className="fas fa-star"></i>
                </button>
            </span>
        );
    }
}

export default RatingButton;