import React, { Component } from 'react';

class CommentInput extends Component {
    render() {
        return (
            <div className="form-group">
                <label htmlFor="comment">Napisz komentarz:</label>
                <textarea className="form-control" rows="5" key="comment" style={{ "minHeight": "100px" }} maxLength="500"></textarea>
                
                <button type="button" class="btn btn-primary m-1">Publikuj</button>
            </div>
        );
    }
}

export default CommentInput;




