import React, { Component } from 'react';
import '../Styles/Login.css';

class Login extends Component {
    render() {
        return (
            <div className="row justify-content-md-center" style={{ "paddingTop": "20px" }}>
                <div className="col-sm-4">
                    <div className="panel">
                        <h2>Logowanie</h2>
                        <p>Wprowadź adres <strong>e-mail</strong> oraz <strong>hasło</strong></p>
                    </div>
                    <form id="Login">
                        <div className="form-label-group">
                            <input type="email" className="form-control" id="inputEmail" placeholder="Adres e-mail" />
                            <label htmlFor="inputEmail">Adres e-mail</label>
                        </div>
                        <div className="form-label-group">
                            <input type="password" className="form-control" id="inputPassword" placeholder="Hasło" />
                            <label htmlFor="inputPassword">Hasło</label>
                        </div>
                        {/* <div className="forgot">
                            <a href="reset.html">Forgot password?</a>
                        </div> */}
                        <button type="submit" className="btn btn-primary" style={{ "width": "100%" }}>Zaloguj</button>
                    </form>
                    <hr style={{ "marginBottom": "5px" }} />
                    <button type="submit" className="btn btn-facebook" style={{ "width": "100%" }}>
                        <i className="fab fa-facebook-f mr-2"></i>
                        Zaloguj przez Facebook'a
                    </button>
                </div>
            </div>
        );
    }
}

export default Login;