import React, { Component } from 'react';

class Registration extends Component {
    render() {
        return (
            <div className="row justify-content-md-center md-form" style={{ "paddingTop": "20px" }}>
                <div className="col-sm-4">
                    <div className="panel">
                        <h2>Rejestracja</h2>
                        <p>Wprowadź dane niezbędne do rejestracji</p>
                    </div>
                    <form id="Registration" className="md-form">
                        <div className="form-label-group">
                            <input type="email" className="form-control" id="inputEmail" placeholder="Adres e-mail*" />
                            <label htmlFor="inputEmail">Adres e-mail*</label>
                        </div>
                        <div className="form-label-group">
                            <input type="password" className="form-control" id="inputPassword" placeholder="Hasło*" />
                            <label htmlFor="inputPassword">Hasło*</label>
                        </div>
                        <div className="form-label-group">
                            <input type="password" className="form-control" id="inputPasswordRepeat" placeholder="Powtórz hasło*" />
                            <label htmlFor="inputPasswordRepeat">Powtórz hasło*</label>
                        </div>
                        <div className="form-label-group">
                            <input type="text" className="form-control" id="inputNickname" placeholder="Nickname*" />
                            <label htmlFor="inputNickname">Nickname*</label>
                        </div>
                        <div className="custom-file" id="customFile" style={{ "marginBottom": "20px" }}>
                            <input type="file" accept="image/*" className="custom-file-input form-control-lg" id="exampleInputFile" style={{ "cursor": "pointer" }} />
                            <label className="custom-file-label" htmlFor="exampleInputFile" >
                                Zdjęcie
                            </label>
                        </div>
                        <button type="submit" className="btn btn-primary" style={{ "width": "100%" }}>Zarejestruj</button>
                    </form>
                    <hr style={{ "margin": "8px 0px" }} />
                    <p style={{ "textAlign": "left" }}>* - pole wymagane</p>
                </div>
            </div>
        );
    }
}

export default Registration;