import React, { Component } from 'react';
import '../Styles/MovieThumbnail.css';

class MovieThumbnail extends Component {
    render() {
        return (
            <div className="col-md-3 p-1">
                <div className="MovieThumbnail img-thumbnail" >
                    <a href={"/images/" + this.props.imageUrl}>
                        <div style={{ "overflow": "hidden" }}>
                            <img className="thumbnailImage" src={"/images/" + this.props.imageUrl} alt={this.props.title} width="100%;" height="90%;" />
                        </div>
                        <div className="caption">
                            <p>{this.props.title}</p>
                        </div>
                    </a>
                </div>
            </div>
        );
    }
}

export default MovieThumbnail;